  margin: 1.25em 0;
  border: 0;
  height: 0;
  border-top: 5px solid #000;
}
.accordion .button-item,
.small-accordion .button-item {
  display: block;
  background-color: #000;
  color: #FFF;
  padding: 0.5em;
  transition: all 250ms;
  position: relative;
  cursor: pointer;
  text-decoration: none;
