  margin-bottom: 0.625em;
}
.accordion .accordion-item:last-of-type,
.small-accordion .accordion-item:last-of-type {
  margin-bottom: 0;
}
.accordion .accordion-item .header,
.small-accordion .accordion-item .header {
  display: block;
  background-color: #000;
  color: #FFF;
  padding: 0.5em;
  transition: all 250ms;
  position: relative;
  cursor: pointer;
  font-size: 14px;
  font-weight: normal;
  border-radius: 3px;
}
.accordion .accordion-item .header:hover,
.small-accordion .accordion-item .header:hover {
  background-color: #666;
}
.accordion .accordion-item .header:after,
.small-accordion .accordion-item .header:after {
  content: "";
  display: block;
  width: 0.625em;
  height: 0.625em;
  transform-origin: center center;
  transform: translateY(-25%) rotate(45deg);
  position: absolute;
  display: block;
  transition: all 250ms cubic-bezier(0.645, 0.045, 0.355, 1);
  top: 50%;
  border-top: 1px solid #FFF;
  border-left: 1px solid #FFF;
  right: 1em;
  z-index: 2;
}
.accordion .accordion-item.active .header:after,
.small-accordion .accordion-item.active .header:after {
  transform: translateY(-75%) rotate(-135deg);
}
.accordion .accordion-item.left-arrow .header:after,
.small-accordion .accordion-item.left-arrow .header:after {
  transform: translate(50%, -50%) rotate(-45deg);
}
.accordion .accordion-item.left-arrow.active .header:after,
.small-accordion .accordion-item.left-arrow.active .header:after {
  transform: translate(0, -75%) rotate(-135deg);
}
