/*
jQuery Cycle Tools
*************************************************************************************/
.cycle-pager {
  position: absolute;
  bottom: 1.25em;
  z-index: 200;
  width: 100%;
  text-align: center;
  margin-top: 1.25em;
  line-height: 10px;
}
.cycle-pager span {
  display: inline-block;
  margin: 0 0.3125em;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: #FFF;
  cursor: pointer;
  text-indent: -999em;
  transition: color 250ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.cycle-pager span.cycle-pager-active {
  background-color: #000;
}

.cycle-next, .cycle-prev {
  width: 1.25em;
  height: 1.25em;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  transform: rotate(-45deg);
  position: absolute;
  display: block;
  transition: all 250ms cubic-bezier(0.645, 0.045, 0.355, 1);
  top: 50%;
  border-top: 0;
  border-left: 0;
  border-bottom: 4px solid #FFF;
  border-right: 4px solid #FFF;
  right: 1.25em;
  z-index: 2;
  margin-top: -0.625em;
  z-index: 200;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.cycle-prev {
  right: auto;
  left: 1.25em;
  border-top: 4px solid #FFF;
  border-left: 4px solid #FFF;
  border-bottom: 0;
  border-right: 0;
}

.slideshow-loader {
